document.addEventListener("DOMContentLoaded", () => {
	// Includes
	// // Variables
const body = document.querySelector('.body');
const isMobileDevice = (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent));
const isSmoothScrollSupported = 'scrollBehavior' in document.documentElement.style;
const media = window.matchMedia("(max-width: 991.98px)");
const burgerBtn = document.querySelector('.burger');
const nav = document.querySelector('.nav');
const burger = new Burger(burgerBtn, nav);
;
	// // Functions
function log() {
	console.log('Hello App');
}
log();

function showActiveLink(links, activeClass, offsetDifference) {
	for (let i = links.length - 1; i >= 0; i--) {
		let target = document.querySelector(links[i].hash);
		if (window.scrollY > target.offsetTop - offsetDifference) {
			if (document.querySelector(`.${activeClass}`)) {
				document.querySelector(`.${activeClass}`).classList.remove(`${activeClass}`);
			}
			links[i].classList.add(`${activeClass}`);
			break;
		} else {
			if (document.querySelector(`.${activeClass}`)) {
				document.querySelector(`.${activeClass}`).classList.remove(`${activeClass}`);
			}
		}
	}
}

function scrollToTarget(id, media) {
	const target = document.querySelector(id);
	if (!isSmoothScrollSupported && target !== null) {
		const bodyRect = document.body.getBoundingClientRect();
		const elemRect = target.getBoundingClientRect();
		const offset = elemRect.top - bodyRect.top;

		const requestAnimFrame = (function () {
			return window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.mozRequestAnimationFrame || function (callback) {
				window.setTimeout(callback, 1000 / 60);
			};
		})();
		Math.easeInOutQuad = function (t, b, c, d) {
			t /= d / 2;
			if (t < 1) {
				return c / 2 * t * t + b
			}
			t--;
			return -c / 2 * (t * (t - 2) - 1) + b;
		};
		function scrollToIos(to, callback, duration) {
			function move(amount) {
				document.documentElement.scrollTop = amount;
				document.body.parentNode.scrollTop = amount;
				document.body.scrollTop = amount;
			}

			function position() {
				return document.documentElement.scrollTop || document.body.parentNode.scrollTop || document.body.scrollTop;
			}
			let start = position(),
				change = to - start,
				currentTime = 0,
				increment = 20;
			duration = (typeof (duration) === 'undefined') ? 500 : duration;

			let animateScroll = function () {
				currentTime += increment;
				let val = Math.easeInOutQuad(currentTime, start, change, duration);
				move(val);
				if (currentTime < duration) {
					requestAnimFrame(animateScroll);
				} else {
					if (callback && typeof (callback) === 'function') {
						callback();
					}
				}
			};
			animateScroll();
		}
		scrollToIos(offset - 50, null, 500);
		return;
	}

	if (target !== null) {
		let pos = target.offsetTop - (media.matches ? 50 : 80);
		window.scrollTo({
			top: pos,
			behavior: 'smooth'
		});
	}
};
	// // BurgerJS
class Burger {
	constructor(btn, list) {
		this.btn = btn;
		this.list = list;
		this.opened = false;
		this.btnDisabled = false;
		this.scrollWidth = window.innerWidth - document.body.clientWidth;

		this.btn.addEventListener('click', () => {
			this.toggle();
		})
	}

	open() {
		this.disableBtn();
		if (this.scrollWidth) {
			document.body.style.paddingRight = `${this.scrollWidth}px`
		}
		this.btn.classList.add('burger--active');
		this.btn.setAttribute('aria-expanded', true);
		this.list.classList.add('nav--active');
		this.list.setAttribute('aria-hidden', false);

		body.classList.add('lock')
	}

	close() {
		this.disableBtn();
		if (this.scrollWidth) {
			document.body.style.paddingRight = null;
		}
		body.classList.remove('lock')
		this.btn.classList.remove('burger--active');
		this.btn.setAttribute('aria-expanded', false);
		this.list.classList.remove('nav--active');
		this.list.setAttribute('aria-hidden', true);
	}

	toggle() {
		if (this.btn.classList.contains('burger--active')) {
			this.close();
		} else {
			this.open();
		}
	}

	disableBtn() {
		this.btn.disabled = true;
		this.list.addEventListener('transitionend', () => {
			this.btn.disabled = false;
		})
	}
};
	// -- //

	if (media.matches) {
		burgerBtn.setAttribute('aria-hidden', false);
		nav.setAttribute('aria-hidden', true);
	}

	const player = new OpenPlayerJS('audio-hero', {
		controls: {
			alwaysVisible: true,
			layers: {
				left: ['play', 'progress', 'time'],
				middle: [],
				right: [],
			}
		},
		mode: 'responsive',
		startVolume: 0.05,
		startTime: 0,
		onError: (e) => console.error(e),
	});

	player.init();
})